.cover {
  background: url("../../../build/images/cover-inicio.webp") no-repeat top center;
  background-size: cover;
  height: 300px;
  width: 100%;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
.cover::after {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  content: "";
  background-color: rgba(0, 0, 0, 0.4);
}
.cover__title {
  z-index: 1;
  position: relative;
  text-align: center;
  color: #fff;
}
@media (min-width: 768px) {
  .cover {
    height: 400px;
  }
}
@media (min-width: 1024px) {
  .cover {
    height: 600px;
  }
}

@media all and (min-width: 400px) {
  .cover {
    padding: 100px 0;
  }
}
/*HOME PAGE ABOUT*/
.cont-about {
  margin: 2rem auto;
  padding: 2rem 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  gap: 3rem;
}
@media (min-width: 768px) {
  .cont-about {
    padding: 5rem 0;
    flex-direction: row;
  }
}
@media (min-width: 768px) {
  .cont-about__text {
    flex: 1 1 50%;
  }
}
@media (min-width: 1024px) {
  .cont-about__text {
    flex: 1 1 45%;
  }
}
.cont-about__text .section-title,
.cont-about__text .formatted-text {
  margin-bottom: 3rem;
}
.cont-about__text .btn-primary {
  max-width: 100%;
  width: 100%;
}
@media (min-width: 1024px) {
  .cont-about__text .btn-primary {
    width: 40rem;
  }
}
.cont-about__img {
  width: 100%;
  height: 30rem;
}
@media (min-width: 768px) {
  .cont-about__img {
    height: 40rem;
    flex: 0 0 45%;
  }
}
@media (min-width: 1024px) {
  .cont-about__img {
    height: 50rem;
    flex: 0 0 50%;
  }
}
.cont-about__img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.cont-icons {
  display: grid;
  justify-content: center;
  background-color: rgba(var(--tertiary-color));
}
.cont-icons__boxes {
  margin: 35px 0;
}
.cont-icons__boxes p {
  width: 20rem;
}

.cont-slider {
  color: rgba(var(--secondary-color), 0.6);
}

.swiper {
  width: 100%;
  height: 100%;
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}